<template>
	<div>
		<div class="examin echarts">
			<div class="bg_white pad_40">
        <el-row class="examine_header">
          <el-col :span="12">
            <h2></h2>
          </el-col>
          <el-col :span="12" class="col_right">
            <el-radio-group v-model="radio2"  @change="changeQueryByDate">
              <el-radio :label="3"></el-radio>
              <el-radio :label="6" ></el-radio>
              <el-radio :label="9" ></el-radio>
            </el-radio-group>
            <el-date-picker
              v-model="value6"
              class="mar_l_15"
              type="daterange"
              :picker-options="pickerOptions0"
              @change="dateChange"
              placeholder="">
            </el-date-picker>
          </el-col>
        </el-row>
        <el-table
          :data="tableData"
          style="width: 100%">
          <el-table-column
            prop="apart"
            label=""
           >
          </el-table-column>
          <el-table-column
            prop="totalCustomerNum"
            label=""
            >
          </el-table-column>
          <el-table-column
            prop="totalInvitationNum"
            label="">
          </el-table-column>
          <el-table-column
            prop="totalSignNum"
            label="">
          </el-table-column>
          <el-table-column
            prop="totalSignAmount"
            label="">
          </el-table-column>
        </el-table>
        <el-row class="echarts_wrap" >
          <el-col :span="13">
            <h3 class="el-title"></h3>
            <div style="height: 300px;">
              <IEcharts :option="bar" :loading="loading" v-if="this.showbar"></IEcharts>
            </div>
          </el-col>
          <el-col :span="2">
            <ul class="data_wrap">
            <li>
              <label class="green"></label><span></span>
            </li>
            <li>
              <label class="yellow"></label><span></span>
            </li>
            <li>
              <label class="blue"></label><span></span>
            </li>
            </ul>
          </el-col>
          <el-col :span="7">
            <h3 class="el-title"></h3>
            <div style="height: 300px;">
              <IEcharts :option="pie"></IEcharts>
            </div>
          </el-col>
          <el-col :span="2">
            <ul class="data_wrap">
              <li>
                <label class="green"></label><span></span>
              </li>
              <li>
                <label class="yellow"></label><span></span>
              </li>
              <li>
                <label class="blue"></label><span></span>
              </li>
            </ul>
          </el-col>
        </el-row>
        <el-row v-if="this.stackLength ?this.showBrokenLine:!this.showBrokenLine">
          <el-col :span="24" style="position: absolute;">
            <h3 class="el-title"></h3>
            <div style="height: 360px; width: 70%;">
              <IEcharts :option="stack"></IEcharts>
            </div>
            <el-select  v-model="selectedValue" placeholder=""  @change="changeTypeEvent">
              <el-option
                v-for="item in options"
                :key="item.selectedValue"
                :label="item.label"
                :value="item.selectedValue">
              </el-option>
            </el-select>
          </el-col>
        </el-row>
      </div>
		</div>

	</div>
</template>
<script type="text/javascript">
	import headerTop from "../common/header.vue";
  import IEcharts from 'vue-echarts-v3/src/full.vue';
  import {cookieUtil} from '../../js/cookie'
  import {getNewDate,getNowDayWeek,getNowDayMonth} from '../../js/getDate'
  import http from '../../axoios/http'
  import api from '../../axoios/api'
	export default{
		name:'entering',
		components:{
      headerTop,
      IEcharts
		},
		data(){
			return {
        options: [{
          selectedValue: '1',
          label: ''
        }, {
          selectedValue: '2',
          label: ''
        }, {
          selectedValue: '3',
          label: ''
        }, {
          selectedValue: '4',
          label: ''
        }],
        selectedValue: '1',
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
        data: [5, 20, 90],
        dataList: {
          data1: '',
          data2: ''
        },
        radio2: 3,
        value6: '',
        showBrokenLine: false,
        tableData: [],
        totalData: {
          department: 4,
          totalCustomerNum: 0,
          totalInvitationNum: 0,
          totalSignNum: 0,
          totalSignAmount: 0
        },
        custNum: [],
        intNum: [],
        signNum: [],
        amountNum: [],
        loading: false,
        bar: {
          title: {
            text: ''
          },
          xAxis: {
            data: ["","",""]
          },
          yAxis: {},
          series: [
            {
              name:'',
              type:'bar',
              //data:fu,
              data:[],
              //
              itemStyle:{
                normal:{color:'#13CE66'}
              }
            },
            {
              name:'',
              type:'bar',
              //data:yun,
              data:[],
              //
              itemStyle:{
                normal:{color:'#F7BA2A'}
              }
            },
            {
              name:'',
              type:'bar',
              //data:qi,
              data:[],
              //
              itemStyle:{
                normal:{color:'#20A0FF'}
              }
            }

          ]
        },
        pie: {
          title: {
            text: '',
            x: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          color: [],
          legend: {
            orient: 'vertical',
            left: 'left',
          },
          series: [{
            name: '',
            type: 'pie',
            radius: '55%',
            center: ['50%', '50%'],
            data: [],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }]
        },
        stack: {
          title: {
            text: ''
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: []
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: []
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: '',
              type: 'line',
              smooth: true,  //
              stack: '1',
              data: [],
              itemStyle: {
                normal: {
                  color: "#13CE66",  //,
                  borderColor: "#13CE66"
                }
              },
            },
            {
              name: '',
              type: 'line',
              smooth: true,
              stack: '2',
              data: [],
              itemStyle: {
                normal: {
                  color: "#F7BA2A",  //
                  borderColor: "#F7BA2A"
                }
              },
            },
            {
              name: '',
              type: 'line',
              smooth: true,
              stack: '3',
              data: [],
              itemStyle: {
                normal: {
                  color: "#20A0FF",  //
                  borderColor: "#20A0FF"
                }
              },
            }
          ]
        },
        stackLength: true,
        params: {
          startTime: "",
          endTime: ""
        },
        chooseData: {
          start: "",
          end: ""
        },
        timeflows:[],
        okeys:[],
        showbar:true,
        val:[],
        department:"",
        key:"",
        paramsData:[],
        sumBusinessData:[],
        sumOperateData:[],
        sumcustomData:[],
        section:"",
        text:"",
        zhaoshang:"",
        yunying:"",
        dakehu:""

      }
		},
		mounted(){
      this.$store.dispatch("setActiveIndex","2");
      //
      this.params.startTime = getNewDate.getDate(0)+" 00:00:00";
      this.params.endTime = getNewDate.getDate(0)+" 00:00:00";
      this.fetchExamineData();  //
		},
		methods:{
		//ps:1-10
    //10-20 20-30

      /**
       * 
       * @param val
       */
      changeTypeEvent(val){
        this.fetchExamineData();
      },
      /**
       * 
       * @param val
       */
      dateChange(val){
        let vm = this;
        this.radio2 = "";
        vm.text=4;
        vm.stack.xAxis.data = [];
        let radio = val.split("-");
        let sRadio = radio[2];
        let dRadio = radio[5];
        //10
        if(sRadio<10){
          sRadio = "0"+sRadio;
        }
        if(dRadio<10){
          dRadio = "0"+dRadio;
        }
        /**
         * 
         * */
        let newRadio = radio[3].replace(/(^\s*)|(\s*$)/g, "");   //
        vm.params.startTime = (radio[0]+"-"+radio[1]+"-"+radio[2])+" 00:00:00";
        vm.params.endTime = (newRadio+"-"+radio[4]+"-"+radio[5])+" 00:00:00";
        /**
         * 
         * */
        vm.chooseData.start = radio[0]+"-"+radio[1]+"-"+radio[2];
        vm.chooseData.end = newRadio+"-"+radio[4]+"-"+radio[5];
        vm.stackLength = false;
        let sDays = radio[5] - radio[2];
        if(sDays=== 0){    //
          vm.stackLength = true;
          vm.fetchExamineData();
        }else if(sDays>0 && sDays<=10){   //1-10
          vm.computeredChooseNum(1,sDays);  //
          vm.section = 1;
          vm.fetchExamineData();
          return
        }else if(sDays>10 && sDays<=20){  //11-20
          vm.computeredChooseNum(2,sDays);
          vm.section = 2;
          vm.fetchExamineData();
          return
        }else if(sDays>10 && sDays<=30){   //21-30
          vm.computeredChooseNum(3,sDays);
          vm.section = 3;
          vm.fetchExamineData();
          return
        }else{
          return
        }
      },
      /**
       * 
       * @param val
       */
      changeQueryByDate(val){
        /**
         * 
         */
        let vm = this;
        vm.stackData();  //function
        vm.fetchExamineData();  //function
      },
      /**
       * function
       */
      stackData(){
        let vm = this;
        let val = vm.radio2;
        let startTime = getNewDate.getDate(0).split("-");
        switch (val){
          case 3:  //
            /**
             * 
             * */
            vm.stackLength = true;
            vm.params.endTime = getNewDate.getDate(0)+" 00:00:00";
            vm.params.startTime = getNewDate.getDate(0)+" 00:00:00";
            break;
          case 6:  //
            vm.stackLength = false;
            /**
             * 
             * */
            vm.params.endTime = getNewDate.getDate(0)+" 00:00:00";
            vm.params.startTime = getNewDate.getDate(-(getNewDate.getNowDayWeek()-1))+" 00:00:00";
            vm.stack.xAxis.data = [];
            /**
             * x
             * */
            let endTime = getNewDate.getDate(-(getNewDate.getNowDayWeek()-1)).split("-");
            let days = startTime[2] - endTime[2];  //
            if(days=== 0){
              this.stackLength = true;    //
            }
            for(let i=0; i<days+1; i++){
              vm.stack.xAxis.data.unshift(getNewDate.getDate(-i));  //data
            }
            break;
          case 9:  //
            vm.stackLength = false;
            /**
             * 
             * */
            vm.params.endTime = getNewDate.getDate(0)+" 00:00:00";
            vm.params.startTime = getNewDate.getNowDayMonth()+" 00:00:00";
            vm.stack.xAxis.data = [];
            /**
             * x
             * */
            let endMonthTime = getNewDate.getNowDayMonth().split("-");
            let oDays = startTime[2] - endMonthTime[2];
            if(oDays=== 0){
              vm.stackLength = true;
            }else if(oDays>0 && oDays<=10){
              vm.computeredNum(1,oDays);   //
              vm.section = 1;
              return
            }else if(oDays>10 && oDays<=20){
              vm.computeredNum(2,oDays);
              vm.section = 2;
              return
            }else if(oDays>10 && oDays<=30){
              vm.computeredNum(3,oDays);
              vm.section = 3;
              return
            }else{
              return
            }
            break;
        }
      },
      /***
       * 
       * @returns {Promise.<void>}
       */
      fetchExamineData: async function() {
        let vm=this;
        /**
         * 
         * */
        vm.sumBusinessData=[];
        vm.sumOperateData=[];
        vm.sumcustomData=[];
        //
        const res = await http.post(api.queryByDate+"?token="+cookieUtil.get("token"), vm.params)
        let result = res.data;
        if(!result){  //
          return
        }
        if(result.code===0 && result.data!==""){
          vm.tableData = result.data.statistics;  //
          vm.timeflows = result.data.timeflows;   //
          /**
           * 
           */
          vm.custNum = [];
          vm.intNum = [];
          vm.signNum = [];
          vm.amountNum = [];
          vm.bar.series[0].data = [];
          vm.bar.series[1].data = [];
          vm.bar.series[2].data = [];
          vm.totalData.totalCustomerNum = vm.totalData.totalSignAmount = vm.totalData.totalSignNum =vm.totalData.totalInvitationNum =0;
          /**
           * 
           * */
          vm.tableData.forEach(function(value,key,arr){
            /**
             * 
             * */
            vm.totalData.totalCustomerNum += value.totalCustomerNum;
            vm.totalData.totalInvitationNum += value.totalInvitationNum;
            vm.totalData.totalSignNum += value.totalSignNum;
            vm.totalData.totalSignAmount += value.totalSignAmount;
            vm.showbar = true;
            if(value.department && (value.department === 2)){   //
              vm.bar.series[1].data=[value.totalCustomerNum,value.totalInvitationNum,value.totalSignNum];
            }else if(value.department && (value.department === 3)){  //
              vm.bar.series[2].data=[value.totalCustomerNum,value.totalInvitationNum,value.totalSignNum];
            }else if(value.department && (value.department === 1)){   //
              vm.bar.series[0].data=[value.totalCustomerNum,value.totalInvitationNum,value.totalSignNum];
            }else{
              vm.showbar = false;
              return
            }
            vm.amountNum.push(value.totalSignAmount);
          });
          vm.tableData = result.data.statistics;
          //
          vm.tableData.push(vm.totalData);
          /**
           * department
           */
          vm.pie.series[0].data =[];
          /**
           * 
           */
          vm.pie.color = [];
          vm.stack.series[0].data =[];
          vm.stack.series[1].data = [];
          vm.stack.series[2].data = [];
          for (let i=0;i<vm.tableData.length;i++){
            let department = vm.tableData[i].department;
            switch (department){
              case 1:
                vm.tableData[i].apart="";
                vm.pie.series[0].data.push({
                  name:"",
                  value:vm.tableData[i].totalSignAmount
                });
                vm.pie.series[0].data[0].value = vm.tableData[i].totalSignAmount;
                vm.pie.color.push("#13CE66");

                vm.stack.legend.data.push("");
                vm.stack.series[0].name = "";
                break;
              case 2:
                vm.tableData[i].apart="";
                vm.pie.series[0].data.push({
                  name:"",
                  value:vm.tableData[i].totalSignAmount
                });
                vm.pie.color.push("#F7BA2A");

                vm.stack.legend.data.push("");
                vm.stack.series[1].name = "";
                break;
              case 3:
                vm.tableData[i].apart="";
                vm.pie.series[0].data.push({
                  name:"",
                  value:vm.tableData[i].totalSignAmount
                });
                vm.pie.color.push("#20A0FF");

                vm.stack.legend.data.push("");
                vm.stack.series[2].name = "";
                break;
              case 4:
                vm.tableData[i].apart="";
                break;
            }
          }
          /**
           * 
           *
           * @fanwenqiang   2017/09/29
           */
          vm.brokenLine();
        }
      },
      /**
       * function
       * */
      brokenLine(){
        let vm = this;
        let xAxis = vm.stack.xAxis.data;
        let timeflows = vm.timeflows;
        if(vm.radio2 === 6){     //
          vm.paramsSumData(vm.stack.series[0].data,vm.stack.series[1].data,vm.stack.series[2].data,xAxis);
          //123x
        }
        if(vm.radio2 === 9 || vm.text === 4 ){  //
          vm.paramsSumData(vm.sumBusinessData,vm.sumOperateData,vm.sumcustomData,vm.paramsData);
          //123

          //data
          let j=1;
          sumData(vm.sumBusinessData,0);
          sumData(vm.sumOperateData,1);
          sumData(vm.sumcustomData,2);
          /***
           *vm.section123
           * */
          function sumData(arr,key) {
            for (let i=0; i<arr.length;i=i+vm.section){
              if(vm.section === 1){
                vm.stack.series[key].data.push(arr[i]);
                j=1;
              }
              if(vm.section === 2){
                if(i === 0){
                  vm.stack.series[key].data.push(arr[0]);
                }else{
                  vm.stack.series[key].data.push((arr[i]+arr[i-1]));
                }
              }
              if(vm.section === 3){
                if(i === 0){
                  vm.stack.series[key].data.push(arr[0]);
                }else{
                  vm.stack.series[key].data.push((arr[i]+arr[i-1]+arr[i-2]));
                }
              }
            }
          }
        }
      },
      /**
       * 4push123
       * 
       * */
      paramsSumData(arr1,arr2,arr3,arr4){
        let vm = this;
        arr4.forEach(function (value,key,Arr) {
          let isPush =false;
          vm.val=[];
          vm.timeflows.forEach(function (val,okey,oArr) {
            let entryDate = val.entryDate;
            let newEntryDate = entryDate.substring(0,entryDate.length-9); //000000
            if(newEntryDate === value){ //
              isPush = true;
              let selectedValue = vm.selectedValue;  //|||
              switch (selectedValue){
                case "1":
                  vm.val.push({
                    param1:val.customerNum,
                    param2:val.department
                  });  //
                  break;
                case "2":
                  vm.val.push({
                    param1:val.invitationNum,
                    param2:val.department
                  });  //val.invitationNum
                  break;
                case "3":
                  vm.val.push({
                    param1:val.signNum,
                    param2:val.department
                  });   //val.signNum
                  break;
                case "4":
                  vm.val.push({
                    param1:val.signAmount,
                    param2:val.department
                  });   ////val.signAmount
                  break;
              }
            }
          })
          /**
           * :
           */
          if(isPush){
            vm.zhaoshang="";
            vm.yunying="";
            vm.dakehu="";
            vm.val.forEach(function (value,key,Arr) {
              if(vm.val[key].param2 ===1){   //
                  vm.zhaoshang = vm.val[key].param2;
                  arr1.push(vm.val[key].param1);
              }
              if(vm.val[key].param2 ===2){   //
                vm.yunying = vm.val[key].param2;
                arr2.push(vm.val[key].param1);
              }
              if(vm.val[key].param2 ===3){   //
                vm.dakehu = vm.val[key].param2;
                arr3.push(vm.val[key].param1);
              }
            });
            if(vm.zhaoshang==""){
              arr1.push(0);
            }
            if(vm.yunying==""){
              arr2.push(0);
            }
            if(vm.dakehu==""){
              arr3.push(0);
            }
          }else{
            arr1.push(0);
            arr2.push(0);
            arr3.push(0);
          }
        })
      },
      /**
       * X
       * @param val  
       * @param oDays    
       */
      computeredNum(val,oDays){
        let vm = this;
        vm.paramsData = [];
        let kDays = parseInt(oDays / val);
        for (let i = 0; i < kDays + 1; i++) {  //
          i===0?vm.stack.xAxis.data.unshift(getNewDate.getDate(0)):vm.stack.xAxis.data.unshift(getNewDate.getDate(-(i* val)));
        }
        for (let j = 0; j < oDays+1; j++){     //
          j===0?vm.paramsData.unshift(getNewDate.getDate(0)):vm.paramsData.unshift(getNewDate.getDate(-j));
        }
        if (oDays % val !== 0) {
          vm.stack.xAxis.data.unshift(getNewDate.getNowDayMonth());
          vm.paramsData.unshift(getNewDate.getNowDayMonth());
        }
      },
      /**
       * X
       * @param val
       * @param sDays    
       */
      computeredChooseNum(val,sDays){
        let vm = this;
        vm.paramsData = [];
        let end = vm.chooseData.end.split("-");
        let kDays = parseInt(sDays / val);
        for (let i = 0; i < kDays + 1; i++) {
          if(i===0){
            vm.stack.xAxis.data.unshift(vm.chooseData.end);
          }else{
            let endNum = end[2]-(i* val);
            if(endNum<10){
              endNum = "0"+endNum
            }
            vm.stack.xAxis.data.unshift(end[0]+"-"+end[1]+"-"+endNum);
          }
        }
        for (let j = 0; j < sDays+1; j++){     //
          let endNum = end[2]-j;
          if(endNum<10){
            endNum = "0"+endNum
          }
          j===0?vm.paramsData.unshift(vm.chooseData.end):vm.paramsData.unshift(end[0]+"-"+end[1]+"-"+endNum);
        }
        if (sDays % val !== 0) {
          vm.stack.xAxis.data.unshift(vm.chooseData.start);
//          vm.paramsData.unshift(vm.chooseData.start);
        }
      }
		},
	}
</script>
<style lang="scss">
  .examin{
    .el-select {
      display: inline-block;
      position: absolute !important;
      width: 110px;
      top: 0;
      right: 30%;
    }
    .el-table{
      margin-bottom: 21px;
    }
    .examine_header{
      height: 68px;
      line-height: 68px;
      h2{
        height: 68px;
        line-height: 68px;
        font-size: 18px;
        color: #1F2D3D;
      }
      .col_right{
        text-align: right;
      }
    }
    .el-title{
      font-size: 18px;
      color: #1F2D3D;
      margin-top: 22px;
    }
    .echarts_wrap{
    }
    .data_wrap{
      padding-top: 68px;
      li{
        margin-bottom: 4px;
        label{
          width: 24px;
          height: 12px;
          border-radius: 2px;
          display: inline-block;
          margin-right: 8px;
        }
        .green{
          background-color: #13CE66;
        }
        .yellow{
          background-color: #F7BA2A;
        }
        .blue{
          background-color: #20A0FF;
        }
      }
    }
  }
  .list_wrap{
    height: 64px;
    line-height: 64px;
    padding-left: 85px;
    a{
      color: #4d4d4d;
      display: block;
      margin-left: 32px;
      float: left;
      &:hover{
        color: #ff6b2c;
      }
    }
  }
  .choose_wrap{
    line-height: 64px;
    .text{
      text-align: center;
    }
    .search-btn{
      width: 118px;
      height: 38px;
      border-radius: 19px;
      background-color: transparent !important;
      color: #4b90ff !important;
      margin-left: 32px;
    }
  }
</style>



